import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CarestaffClassComponent } from './carestaff-class.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { MaterialModule } from 'src/app/dealer/material.module';
import { AddCarestaffClassComponent } from './add-carestaff-class/add-carestaff-class.component';
import { EditCarestaffClassComponent } from './edit-carestaff-class/edit-carestaff-class.component';

const routes: Routes = [
  {
    path: '',
    component: CarestaffClassComponent,
  },
];

@NgModule({
  declarations: [CarestaffClassComponent, AddCarestaffClassComponent, EditCarestaffClassComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MaterialModule,
    FormsModule,
    RouterModule.forChild(routes),
  ]
})
export class CarestaffClassModule { }
